.shape{
    --pointer-border: 0;
    --pointer-border-hover: 0;
    --pointer-border-focus: 0;
    --pointer-border-radius: 0;
}

.shape-triangle .pointer-shape,
.shape2-triangle .pointer-2 .pointer-shape{
    -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.shape-rhombus .pointer-shape,
.shape2-rhombus .pointer-2 .pointer-shape{
    -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}

.shape-trapezoid .pointer-shape,
.shape2-trapezoids .pointer-2 .pointer-shape{
    -webkit-clip-path: polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);
    clip-path: polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);
}

.shape-parallelogram .pointer-shape,
.shape2-parallelogram .pointer-2 .pointer-shape{
    -webkit-clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
    clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
}

.shape-right-arrow .pointer-shape,
.shape2-right-arrow .pointer-2 .pointer-shape{
    -webkit-clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
    clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
}

.shape-star .pointer-shape,
.shape2-star .pointer-2 .pointer-shape{
    -webkit-clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
    clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
}